@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
:root {
  --ff-primary: "Quicksand", sans serif;

  --clr-light: #f2f2f2;
  --clr-accent: #f1356d;
  --clr-dark: #333;

  --fs-h1: 3rem;
  --fs-h2: 2.25rem;
  --fs-h3: 1.25rem;
  --fs-body: 1rem;
  
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-size: 1rem;
  font-size: var(--fs-body);
  line-height: 1.6;
  color: #333;
  color: var(--clr-dark);
  font-family: "Quicksand", sans serif;
  font-family: var(--ff-primary);
}

.container {
  max-width: 1100px;
  margin: 0 auto;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border-bottom: 1px solid #f2f2f2;
  border-bottom: 1px solid var(--clr-light);

}

.header h1 {
  color: #f1356d;
  color: var(--clr-accent);
  font-size: 3rem;
  font-size: var(--fs-h1);
  margin: 0;
}

.title-container {
  padding: 1em;
}

.subtitle {
  display: block;
  font-size: 1.25rem;
  font-size: var(--fs-h3);
  text-align: center;
}

.nav {
  padding-top: 1em;
}

.nav ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  list-style: none;
  padding: 0;
}

.nav ul li {
  margin-left: 1em;
}

.nav a {
  text-decoration: none;
  color: #333;
  color: var(--clr-dark);
  font-size: 1.25rem;
  font-size: var(--fs-h3);
  padding: 1em;
}

.nav a:hover, .nav a:focus {
  color: #f1356d;
  color: var(--clr-accent);
}

/* blog previews / list */
.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  color: var(--clr-accent);
  margin-bottom: 8px;
}

.blog-preview a {
  text-decoration: none;
  color: #333;
  color: var(--clr-dark);
}

.author-box {
  width: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

/* blog details page */
.blog-details h2 {
  font-size: 20px;
  color: #f1356d;
  color: var(--clr-accent);
  margin-bottom: 10px;
}
.blog-details div {
  margin: 20px 0;
}
.blog-details button {
  background: #f1356d;
  background: var(--clr-accent);
  color: #f2f2f2;
  color: var(--clr-light);
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* create new blog form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  color: var(--clr-accent);
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  background: var(--clr-accent);
  color: #f2f2f2;
  color: var(--clr-light);
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
